@import './../../styles/variables.scss';

.form{
  width: 40%;
  position:relative;
  margin-bottom: 50px;
  @media(max-width: $mobile){
    width: 80%;
  }
}

.form::before{
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid $very-dark-grayish-blue;
  position:absolute;
  top: 10px;
  left: 20px;
} 

.formInput{
  font-family: "Josefin Sans", sans-serif;
  width: 100%;
  height: 40px;
  background-color: var(--list);
  color: $very-dark-grayish-blue;
  border-radius: 3px;
  border: 0;
  outline: 0;
  font-size: .7rem;
  padding-left: 3rem;
  @media(min-width: $desktop){
    font-size: 1.3rem;
  }
}
